import { AbstractIntAccessArray } from "./AbstractIntAccessArray.js";
import { unflattenDeep } from "../utils.js";
/**
 * 结构可以是嵌套的
 */
export class TypeDeep extends AbstractIntAccessArray {
    constructor() {
        super(...arguments);
        /**
         * 最少返回 1
         */
        this.length = 1;
        /**
         * 记录每次嵌套的长度
         *
         * 比如: `int[2][3]` => [2, 3]
         */
        this.deeps = [];
        this.isList = false;
    }
    intAccess(i) {
        const _next = new TypeDeep();
        Object.setPrototypeOf(_next, this);
        _next.isList = true;
        _next.deeps = [...this.deeps, i];
        _next.length = _next.deeps.reduce((acc, it) => (acc *= it), 1);
        return _next;
    }
    unflattenDeep(result, isString = false) {
        return this.isList
            ? unflattenDeep(result, this.deeps, isString)
            : result[0];
    }
    resultEach(obj, cb) {
        return this.unflattenDeep(this.each(obj, cb));
    }
    each(obj, cb) {
        if (this.isList && Array.isArray(obj))
            obj = obj.flat();
        else
            obj = [obj];
        const res = [];
        for (let i = 0; i < this.length; i++) {
            res.push(cb(obj[i], i));
        }
        return res;
    }
}
