import { createDataView } from "../utils.js";
import { StructType } from "./StructType.js";
/**
 * ## bit-fields
 *
 * ```c++
 *struct T {
 *  uint8_t a : 1;
 *  uint8_t b : 2;
 *  uint8_t c : 3;
 *};
 *int main()
 *{
 *  T t{ 1,2,3 };
 *  printf("%p\n", &t); // *&t === `0001 1101` === `0x1D`
 *  printf("size: %d\n", sizeof(t)); // 1
 *  printf("%d\n", t.a); // 1
 *  printf("%d\n", t.b); // 2
 *  printf("%d\n", t.c); // 3
 *  return 0;
 *}
 * ```
 *
 * ## example
 * ```ts
 * const bf = bitFields(uint8_t, {
 *  a: 1,
 *  b: 2,
 *  c: 3,
 * });
 *
 * bf.dncode( new Uint8Array([0x1D]) ); // { a:1, b:2, c:3 }
 *
 * bf.encode({ a:1, b:2, c:3 });// <1D>
 * ```
 */
export class BitFieldsType extends StructType {
    constructor(size, bitFields) {
        super(size, true);
        this.bitFields = bitFields;
    }
    decode(view, options) {
        const data = super.decode(view, options);
        let i = 0;
        const _getValue = (data, len) => {
            let val = 0;
            let count = 0;
            while (len--) {
                const b = (data >> i) & 1;
                val |= b << count;
                i++;
                count++;
            }
            return val;
        };
        return this.resultEach(data, (num) => {
            i = 0;
            return Object.entries(this.bitFields).reduce((acc, [k, len]) => {
                acc[k] = _getValue(num, len);
                return acc;
            }, {});
        });
    }
    encode(obj, options) {
        const v = createDataView(this.byteLength, options?.view);
        const _getValue = (obj) => {
            let val = 0;
            let count = 0;
            Object.entries(obj).forEach(([k, v]) => {
                const len = this.bitFields[k];
                if (len !== undefined) {
                    val |= v << count;
                    count += len;
                }
            });
            return val;
        };
        let offset = options?.offset ?? 0;
        this.each(obj, (it) => {
            v[this.set](offset, _getValue(it), options?.littleEndian);
            offset += this.size;
        });
        return v;
    }
}
