import { StructType } from "./StructType.js";
export class BoolType extends StructType {
    constructor(type) {
        super(type.size, type.unsigned);
    }
    decode(view, options) {
        let r = super.decode(view, options);
        return this.resultEach(r, (it) => {
            return Boolean(it);
        });
    }
    /**
     * 将obj转换为0或1来储存
     */
    encode(obj, options) {
        const convertObj = this.each(obj, (it) => Number(Boolean(it)));
        return super.encode(convertObj, options);
    }
}
